.searchContainer{
    display: flex;
    justify-content: center;
}

.searchBox{
    position: relative;
}

.searchButton{
    position: absolute;
    right: 15px;
    top: 0;
    height: 100%;
}

.searchInput{
    font-size: 1.2rem;
    height: 30px;
    border-radius: 10px;
    padding: 5px 30px 5px 10px;
    border: 1px solid #fff;
}

.searchInput:focus,
.searchButton:focus{
    outline: none;
    border: 1px solid #ff0000;
    box-shadow: 0 0 5px #ff001f;
}
